body {
		font-family: 'Lucida Console', monaco, monospace ;
	}

.appDiv {
	position: fixed;
}

.mainDiv {
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
}

#projects_container {
	display: block;
	height: 100%;
	width: 80%;
	text-align: left;
	margin-top: 5em;
	position: absolute;
}

.project_item {
		margin-bottom: 2em;
		font-size: 0.8em;
}

#options {
	font-family: 'Lucida Console', monaco, monospace ;
}

#option_select {
	font-family: 'Lucida Console', monaco, monospace ;
}


@media only screen and (min-width: 990px) {
	#projects_container {
		margin-left: 25%;
	}

	input {
		margin-right: 2em;
	}

	.project_info {
		width: 80%;
		margin-bottom: 2em;
		margin-left: 2em;
		font-size: 0.8em;
		display: inline-block;

	}

	.project_image {
		width: 25em;
	}

	.project_writeup {
		vertical-align: top;
		margin-top: 0;
		margin-left: 0.5em; 
		word-wrap: break-word;
		display: inline-block;
	}

	#title, #profile {
		display: inline-block;
	}



	#title {
		text-align: center;
		font-size: 1.5em;

		line-height: .9;
		padding-left: 10%;
		width: 40%;
	}

	#title_content {
		display: inline-block;
		text-align: left;
	}


	#profile {
		font-size: 0.8em;
		margin-left: 5%;

	}


	#tagline {
		font-size: .7em;
	}

	.header {
		display: block;
		font-size: 1.1em;
		line-height: 0.5;
		padding-top: 1em;
	}

	.item {
		padding-left: 1%;
		margin-top: 0px;
		/*line-height: 0.2;*/
		font-size: 0.9em;
	}
}


@media only screen and (max-width: 990px)  {
	#projects_container {
		margin-left: 5%;
	}

	input {
		margin-right: 2em;
	}

	.project_info {
		width: 80%;
		margin-bottom: 2em;
		margin-left: 2em;
		font-size: 0.8em;
		display: inline-block;

	}

	.project_image {
		width: 25em;
	}

	.project_writeup {
		vertical-align: top;
		margin-top: 0;
		margin-left: 0.5em; 
		word-wrap: break-word;
		display: inline-block;
	}


	#title {
		text-align: left;
		font-size: 1.5em;

		line-height: .9;
		padding-left: 3%;
	}

	#title_content {
		display: inline-block;
		text-align: left;
		width: 80%;
	}


	#profile {
		font-size: 0.8em;
		margin-left: 3%;

	}


	#tagline {
		font-size: .7em;
		width: 800%;
	}

	.header {
		display: block;
		font-size: 1.1em;
		line-height: 0.5;
		padding-top: 1em;
	}

	.item {
		padding-left: 1%;
		margin-top: 0px;
		/*line-height: 0.2;*/
		font-size: 0.9em;
	}
	
}
